.jumbotron-wrapper {
  overflow: hidden;

  .container {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding-top: 96px;
    margin-left: 120px;

    .left {
      width: 450px;
      margin-right: 120px;
      margin-top: 25px;

      .logo {
        width: 197px;
        height: 44px;
        margin-bottom: 40px;
      }

      .title {
        font-style: normal;
        font-weight: 800;
        font-size: 48px;
        line-height: 56px;
        letter-spacing: -0.025em;
        color: #001935;
        margin-bottom: 24px;
      }

      .info {
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 28px;
        color: #6b7280;
        margin-bottom: 40px;
      }

      .button {
        background: #0165d5;
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
        border-radius: 4px;
        padding: 12px 24px;
        color: #fff;
        text-decoration: none;
      }
    }

    .right {
      display: flex;
      flex-direction: row;
      flex: 1;
      width: 100%;

      .img-1 {
        width: 400px;
        height: 500px;
        margin-left: 16px;
      }

      .img-2 {
        width: 258px;
        height: 500px;
        margin-left: 16px;
      }
    }
  }
}
