@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.jumbotron-wrapper{overflow:hidden}.jumbotron-wrapper .container{width:100%;display:flex;flex-direction:row;padding-top:96px;margin-left:120px}.jumbotron-wrapper .container .left{width:450px;margin-right:120px;margin-top:25px}.jumbotron-wrapper .container .left .logo{width:197px;height:44px;margin-bottom:40px}.jumbotron-wrapper .container .left .title{font-style:normal;font-weight:800;font-size:48px;line-height:56px;letter-spacing:-0.025em;color:#001935;margin-bottom:24px}.jumbotron-wrapper .container .left .info{font-style:normal;font-weight:normal;font-size:20px;line-height:28px;color:#6b7280;margin-bottom:40px}.jumbotron-wrapper .container .left .button{background:#0165d5;box-shadow:0px 1px 2px rgba(0,0,0,0.05);border-radius:4px;padding:12px 24px;color:#fff;text-decoration:none}.jumbotron-wrapper .container .right{display:flex;flex-direction:row;flex:1 1;width:100%}.jumbotron-wrapper .container .right .img-1{width:400px;height:500px;margin-left:16px}.jumbotron-wrapper .container .right .img-2{width:258px;height:500px;margin-left:16px}body{margin:0;font-family:'Inter', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}h1,h2,h3,h4,h5,p{-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0}

